@font-face {
	font-family: "Montserrat";
	font-display: swap;
	src: local(""), url("../assets/fonts/Montserrat-Regular.woff2") format("woff2"),
		/* Chrome 26+, Opera 23+, Firefox 39+ */ url("../assets/fonts/Montserrat-Regular.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
	font-weight: 400;
	font-style: normal;
}
@font-face {
	font-family: "Montserrat";
	font-display: swap;
	src: local(""), url("../assets/fonts/Montserrat-Medium.woff2") format("woff2"),
		/* Chrome 26+, Opera 23+, Firefox 39+ */ url("../assets/fonts/Montserrat-Medium.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
	font-weight: 500;
	font-style: normal;
}
@font-face {
	font-family: "Montserrat";
	font-display: swap;
	src: local(""), url("../assets/fonts/Montserrat-Bold.woff2") format("woff2"),
		/* Chrome 26+, Opera 23+, Firefox 39+ */ url("../assets/fonts/Montserrat-Bold.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
	font-weight: 700;
	font-style: normal;
}
@import "~normalize.css";

*,
*.after,
*.before {
	box-sizing: border-box;
}

body {
	font-family: "Montserrat", serif;
	background: var(#{--body-bg-default});
}
.container {
	max-width: 1400px;
	margin: 0 auto;
	padding: 0 1rem;
	padding-top: 100px;
}
